import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
var FaAngleUp = require('react-icons/lib/fa/angle-up');
var FaAngleDown = require('react-icons/lib/fa/angle-down');
var FaExternalLink = require('react-icons/lib/fa/external-link');
class Projects extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedPhoto : 0
    }
    this.getPhotoUp = this.getPhotoUp.bind(this);
    this.getPhotoDown = this.getPhotoDown.bind(this);
  }
  getPhotoUp() {
    var photosList = [0,1,2,3,4];
    var nextPhoto = photosList[this.state.selectedPhoto + 1];
    if(nextPhoto !== undefined){
      this.setState(function(){
        return {
          selectedPhoto : nextPhoto
        }
      })
    }
  }
  getPhotoDown() {
    var photosList = [0,1,2,3,4];
    var nextPhoto = photosList[this.state.selectedPhoto - 1];
    if(nextPhoto !== undefined){
      this.setState(function(){
        return {
          selectedPhoto : nextPhoto
        }
      })
    }
  }
  render() {
    var select = 'my-photo'+ this.state.selectedPhoto
    var projectLinks = {
      'my-photo1' : 'https://github-battle-e1213.firebaseapp.com/',
      'my-photo3' : 'https://connect-four-88bb2.firebaseapp.com/',
      'my-photo4' : 'https://guess-game-905a0.firebaseapp.com/',
      'my-photo2' : 'https://hangman-c173a.firebaseapp.com/'
    }
    return (
      <div className='projects'>
        <div className='portname-title'>
          <div className='projects-list' data-aos="fade-right">
            <div className='pup-button'>
            {this.state.selectedPhoto !== 0
              ? <FaAngleUp className='pup-icon' onClick={this.getPhotoDown}/>
              : null
            }
            </div>
            <div className='p-photos'>
                <div className = {select} >
                  <a href = {projectLinks[select]} className='url'></a>
                </div>
            </div>
            <div className='pdown-button' >
              {this.state.selectedPhoto !== 4
                ? <FaAngleDown className='pup-icon' onClick={this.getPhotoUp}/>
                : null
              }

            </div>
          </div>
          <div className='my-info' data-aos="fade-left">
            <div className='info-container'>
              <h3> Projects</h3>
              <p className='pinfo'> Welcome, here are some of my more recent projects I have been working on. Feel free to check out my
              <a href='https://github.com/jordynMcginnis' className = 'github'> Github </a> or email me at <a className='email'> jordynbmcginnis@gmail.com</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Projects


